export function isMatrixBusy(matrix, { x, y }, figure) {
  if (x >= matrix[0].length || y >= matrix.length || x < 0 || y < 0) return true;
  
  for (let mY = y; mY < y + figure.length; mY++) {
    if(mY >= matrix.length) return true;
    const matrixRow = matrix[mY];
    const figureRow = figure[mY - y];
    for (let mX = x; mX < x + figureRow.length; mX++) {
      if(mX >= matrixRow.length) return true;
      if (matrixRow[mX] && figureRow[mX - x]) {
        return true;
      }
    }
  }
  return false;
}

export function isMatrixHasSlotForFigure(matrix, figure) {
  return matrix.some((row, y) => row.some((_, x) => !isMatrixBusy(matrix, {x, y}, figure)));
}

export function writeToMatrix(matrix, { x, y }, figure) {
  const newMatrix = matrix.slice(0, y);
  for (let mY = y; mY < matrix.length; mY++) {
    const matrixRow = matrix[mY];
    const fY = mY - y;
    if (fY >= figure.length) {
      newMatrix.push(matrixRow);
      continue;
    }

    const figureRow = figure[fY];
    const newMatrixRow = matrixRow.slice(0, x);

    for (let mX = x; mX < x + figureRow.length; mX++) {
      if(mX >= matrixRow.length) throw(`failed index mX: ${mX}, matrix row lenght is ${matrixRow.length}`);
      
      if (matrixRow[mX] && figureRow[mX - x]) {
        throw('Matrix cell is bussy');
      }

      newMatrixRow.push(matrixRow[mX] || figureRow[mX - x]);
    }

    newMatrixRow.push(...matrixRow.slice(x + figureRow.length));

    newMatrix.push(newMatrixRow);
  }

  return newMatrix;
}

export function cleanFilledLines(matrix) {
  const affecterRows = [];
  const affectedCols = [];

  const checkCol = (matrix, x) => {
    for (let y = 0; y < matrix.length; y++) {
      if (!matrix[y][x]) return false;
    }
    return true;
  }

  const checkRow = (matrix, y) => {
    return matrix[y].every(i => i)
  }

  const cleanRow = (matrix, y) => {
    return [
      ...matrix.slice(0, y),
      Array(matrix[y].length).fill(0),
      ...matrix.slice(y + 1),
    ];
  }

  const cleanColumn = (matrix, x) => {
    const newMatrix = [];
    for(let y = 0; y < matrix.length; y++) {
      const newRow = [...matrix[y]];
      newRow[x] = 0;
      newMatrix.push(newRow);
    }
    return newMatrix;
  }

  
  for(let y = 0; y < matrix.length; y++) {
    if (checkRow(matrix, y)) {
      affecterRows.push(y);
    }
  }

  for(let x = 0; x < matrix[0].length; x++) {
    if (checkCol(matrix, x)) {
      affectedCols.push(x);
    }
  }

  let newMatrix = matrix;
  if (affectedCols.length || affecterRows.length) {
    if (affectedCols.length) {
      newMatrix = affectedCols.reduce((matrix, x) => cleanColumn(matrix, x), newMatrix); 
    }

    if (affecterRows.length) {
      newMatrix = affecterRows.reduce((matrix, y) => cleanRow(matrix, y), newMatrix);
    }
  }

  return {
    newMatrix,
    affectedCols: affectedCols.length,
    affecterRows: affecterRows.length,
  };
}