import React, { useLayoutEffect, useRef, useState, useEffect } from 'react';
import { Graphics, Container } from 'react-pixi-fiber';
import { animated, useSpring } from 'react-spring';

import { Block } from './Block';

const GridHorizontalLine = ({ length, ...props }) => {
  const gRef = useRef();
  useLayoutEffect(() => {
    const g = gRef.current;
    g.lineStyle(1, 0xD38F00);
    g.lineTo(length, 0);
  }, [length])

  return (
    <Graphics ref={gRef} {...props} />
  );
}

const GridVerticallLine = ({ length, ...props }) => {
  const gRef = useRef();
  useLayoutEffect(() => {
    const g = gRef.current;
    g.lineStyle(1, 0xD38F00);
    g.lineTo(0, length);
  }, [])

  return (
    <Graphics ref={gRef} {...props} />
  );
}

const Grid = ({ sizeX, sizeY, cellWidth, cellHeight }) => {
  return (
    <>
      {Array(sizeY - 1).fill().map((_, i) => (
        <GridHorizontalLine key={i} x={0} y={(i + 1) * cellHeight} length={cellWidth * sizeX} />
      ))}
      {Array(sizeX - 1).fill().map((_, i) => (
        <GridVerticallLine key={`${i}-`} y={0} x={(i + 1) * cellWidth} length={cellHeight * sizeY} />
      ))}
    </>
  );
}

const AnimatedBlock = animated(Block);

const PlayingFieldBlock = (
  { isVisible, width, height, ...props },
) => {
  const [visibilityState, setVisibilityState] = useState(isVisible);

  

  const [animatedProps, animatedApi] = useSpring(() => ({
    from: { width, height },
  }));

  useEffect(() => {
    if (visibilityState === isVisible) return;

    if (visibilityState) {
      animatedApi.start({ width: 1, height: 1, config:{ duration: 200}, onRest: ()=> {
        setVisibilityState(isVisible);
      }});
    } else if(isVisible) {
      animatedApi.set({ width, height });
      setVisibilityState(isVisible);
    }
  }, [isVisible]);

  return (
    <AnimatedBlock
      visible={ visibilityState }
      width={width}
      height={height}
      { ...props }
      { ...animatedProps }
    />
  );
}

export const PlayingField = React.memo(React.forwardRef(({
  gameMatrix,
  sizeX, sizeY, cellWidth, cellHeight,
  blockWidth, blockHeight, blockMargin,
  ...props
}, ref) => {

  const blocksList = [];

  for(let y = 0; y < gameMatrix.length; y++) {
    const row = gameMatrix[y];
    for(let x = 0; x < row.length; x++) {
      
        blocksList.push(
          <PlayingFieldBlock
            key={`${x}:${y}`}
            isVisible={row[x]}
            width={blockWidth - blockMargin * 2}
            height={blockHeight - blockMargin * 2}
            anchor={0.5}
            x={x * blockWidth + blockWidth / 2}
            y={y * blockHeight + blockHeight / 2}
          />);
    }
  }

  return (
    <Container ref={ref} {...props}>
      <Grid sizeX={sizeX} sizeY={sizeY} cellWidth={cellWidth} cellHeight={cellHeight}></Grid>
      {blocksList}
    </Container>
  );
}));
