// https://codesandbox.io/embed/0qkll4y5nw

import React, { useState, useEffect } from 'react';
import { Stage, Text } from 'react-pixi-fiber';

import { isMatrixBusy, writeToMatrix, cleanFilledLines, isMatrixHasSlotForFigure } from './utils/matrix';
import { rotateFigureRight } from './utils/figure';
import { availableFigures } from './common/figures';
import { PlayingField } from './cmp/PlayingField';
import { ScoreText } from './cmp/ScoreText';
import { SlotsWithFigures } from './cmp/SlotsWithFigures';
import { isMobileView, scrHeight, scrWidth } from './utils/other';
import './App.css';
import { useRef } from 'react';
import { TextStyle } from 'pixi.js';
import { PixiButton } from './cmp/PixiButton';





const cellSize = isMobileView ? Math.round((scrWidth - 20) / 10) : 50;

const InitialGameState = {
  gameScore: {
    score: 0,
    rows: 0,
    cols: 0,
  },
  gameMatrix: Array(10).fill(Array(10).fill(0)),
  countFigures: 0,
}

function StageContent() {
  const [figures, setFigures] = useState([]);

  const [gameScore, setGameScore] = useState(InitialGameState.gameScore);

  const [countFigures, setCountFigures] = useState(InitialGameState.countFigures);
  const [isGameOver, setIsGameOver] = useState(false);

  const playingFieldX = 10;
  const playingFieldY = 70;

  const [gameMatrix, setGameMatrix] = useState(InitialGameState.gameMatrix);


  const playingFieldRef = useRef();

  const handleDragEnd = ({ getLocalPosition, figure}) => {
    const pos = getLocalPosition(playingFieldRef.current);
    const matrixCoords = {
      x: Math.round((pos.x) / cellSize),
      y: Math.round((pos.y) / cellSize),
    };

    if (!isMatrixBusy(gameMatrix, matrixCoords, figure.figure)) {
      setCountFigures(countFigures + 1);
      setGameMatrix(writeToMatrix(gameMatrix, matrixCoords, figure.figure));

      setFigures(figures.filter(({ slot }) => figure.slot !== slot));
    }
  }

  function handleOnClick(clickedFigure) {
    setFigures(figures.map((figure) => {
      if (figure === clickedFigure) {
        return {
          ...figure,
          figure: rotateFigureRight(figure.figure),
        };
      }

      return figure;
    }));
  }

  function handleOnClickNewGame() {
    setFigures([]);
    setCountFigures(InitialGameState.countFigures);
    setGameMatrix(InitialGameState.gameMatrix);
    setGameScore(InitialGameState.gameScore);
    setIsGameOver(false);
  }

  useEffect(() => {
    const { newMatrix, affectedCols, affecterRows } = cleanFilledLines(gameMatrix);

    if (affectedCols || affecterRows) {
      setGameMatrix(newMatrix);
      
      /** cacl score */
      const commonAffectedLines = affectedCols + affecterRows;
      setGameScore({
        score: gameScore.score + (commonAffectedLines * commonAffectedLines),
        rows: gameScore.rows + affecterRows,
        cols: gameScore.cols + affectedCols,
      });
    }
  }, [gameMatrix]);

  useEffect(() => {

    if (!figures.length) {
      const nextFigures = [];
      for (let i = 0; i < 3; i++) {
        const figure = availableFigures[getRandomInt(availableFigures.length)];
        nextFigures.push({
          figure,
          slot: i,
        });
      }

      setFigures(nextFigures);
    }
  }, [figures]);


  useEffect(() => {
    const hasSteps = !figures.length || figures.some(({ figure }) => {
      
      let nextFigure = figure;
      for(let i = 0; i < 4; i++) {
        if(isMatrixHasSlotForFigure(gameMatrix, nextFigure)) return true;
        nextFigure = rotateFigureRight(nextFigure);
      }
    });

    if (!hasSteps) {
      setIsGameOver(true);
    }
  }, [figures]);

  useEffect(() => {
    const gameState = loadGameState();
    if (gameState) {
      setFigures(gameState.figures);
      setCountFigures(gameState.countFigures);
      setGameMatrix(gameState.gameMatrix);
      setGameScore(gameState.gameScore);
    }
  }, []);

  useEffect(() => {
    saveGameState(gameMatrix, gameScore, countFigures, figures);
  }, [gameMatrix, gameScore, countFigures, figures]);

  const slotWithFiguresOpts = isMobileView
   ? { 
      animateOnPress: true,
      height: scrHeight, width: scrWidth - 10,
      x: 10,
      y: playingFieldY + cellSize * 10 + 10,
      view: 'horizont'
    }
    : { height: 550, width: 250, x: 550, y: 30 };

  const gameOverFontSize = isMobileView ? scrWidth / 7 : 86;
  const gameOverPos = {
    x: playingFieldX + cellSize * 5,
    y: playingFieldY + cellSize * 4,
  };

  const newGameBtnPos = {
    x: gameOverPos.x,
    y: gameOverPos.y + gameOverFontSize + 20
  }

  return (
    <>
      <PlayingField 
        ref={playingFieldRef}
        x={playingFieldX}
        y={playingFieldY}
        gameMatrix={gameMatrix}
        blockMargin={1}
        blockWidth={cellSize}
        blockHeight={cellSize}
        sizeX={10} sizeY={10} cellWidth={cellSize} cellHeight={cellSize}
      />
      <SlotsWithFigures
        {...slotWithFiguresOpts}
        blockMargin={1}
        blockWidth={cellSize}
        blockHeight={cellSize}
        figures={figures}
        onDragEnd={handleDragEnd}
        onClick={handleOnClick}
      />

      <ScoreText {...gameScore} figures={countFigures} x={10} y={0} />
      {isGameOver && (
        <>
          <Text
            {...gameOverPos}
            text="Game Over"
            anchor={.5}
            style={
              new TextStyle({
                fontSize: gameOverFontSize,
                fontWeight: 400,
                fill: '#FBA41C',
                stroke: '#f77a0a',
                strokeThickness: 10,
              })
            }
          />
          <PixiButton {...newGameBtnPos} onClick={handleOnClickNewGame} text="Start New Game" />
        </>
      )}
    </>

  );
}



const App = () => {

  const opts = isMobileView ? { height: scrHeight, width: scrWidth} : { height: 600, width: 800 };
  return (
    <div>
      <Stage options={{ backgroundColor: 0x664131, ...opts }}>
        <StageContent />
      </Stage>
    </div>
  );
}

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

function saveGameState(gameMatrix, gameScore, countFigures, figures) {
  localStorage.setItem('gamestate.1', JSON.stringify({
    gameMatrix,
    gameScore,
    countFigures,
    figures,
  }));
}

function loadGameState() {
  const jsonStr = localStorage.getItem('gamestate.1');
  if (!jsonStr) return null;
  try {
    const data = JSON.parse(jsonStr);
    return data;

  } catch(e) {
    return null;
  }
}

export default App;
