import React, { useRef, useLayoutEffect, useMemo, useState } from 'react';
import { Container, Graphics, Text } from 'react-pixi-fiber';
import { TextStyle, Rectangle } from 'pixi.js';
export function PixiButton({
  x,
  y,
  text,
  onClick,
  width = 200,
  height = 50,
  borderRadius=11,
  borderColor=0xf77a0a,
  borderWidth=5,
  fillColor=0xFBA41C,
  textColor=0xf77a0a,
  ...props
}) {

  const cntRef = useRef();
  const gRef = useRef();
  useLayoutEffect(() => {
    const g = gRef.current;

    g.lineStyle(borderWidth, borderColor, 1);
    g.beginFill(fillColor);
    g.drawRoundedRect(0, 0, width, height, borderRadius);
    g.endFill();

  }, [width, height]);

  const [isPressed, setIsPressed] = useState();

  const handlePointerDown = () => {
    setIsPressed(true);
  }

  const handlePointerUp = () => {
    setIsPressed(false);
    if (isPressed && onClick) {
      onClick();
    }

  }

  const handlePointerOut = () => {
    setIsPressed(false);
  }

  const hitArea = useMemo(() => {
    return new Rectangle(0,0, width, height);
  }, [width, height]);

  const pivot = useMemo(() => ({x: width / 2, y: height / 2}), [width, height]);

  return (
    <Container
      ref={cntRef}
      x={x} 
      y={y} 
      hitArea={hitArea} 
      interactive={true}
      pivot={pivot}
       scale={isPressed ? 1.05 : 1}
      pointerup={handlePointerUp} 
      pointerdown={handlePointerDown}
      pointerupoutside={handlePointerOut}
    >
        <Container >
          <Graphics ref={gRef} {...props} />
          <Text
            x={width / 2}
            y={height / 2}
            text={text}
            anchor={.5}
            style={
              new TextStyle({
                fontSize: 20,
                fontWeight: 700,
                fill: textColor,
              })
            }
          />
      </Container>
    </Container>
  );
}