
import React from 'react';
import { Container, Text } from 'react-pixi-fiber';
import { TextStyle } from 'pixi.js';

export const ScoreText = ({ score, rows, cols, figures, ...props }) => {
  const lines = rows + cols
  const eff = lines ? `${Math.round(score / figures * 100) }%` : '-/- ';
  return (
    <Container {...props}>
      <Text
        text={`Score: ${score}\nEfficiency: ${eff}`}
        style={
          new TextStyle({
            // align: 'center',
            // fontFamily: '"Source Sans Pro", Helvetica, sans-serif',
            fontSize: 30,
            fontWeight: 400,
            fill: '#FBA41C',
            // stroke: '#01d27e',
            // strokeThickness: 5,
            letterSpacing: 3,
            // dropShadow: true,
            // dropShadowColor: '#ccced2',
            // dropShadowBlur: 4,
            // dropShadowAngle: Math.PI / 6,
            // dropShadowDistance: 6,
            // wordWrap: true,
            // wordWrapWidth: 440,
          })
        }
      />

      <Text
        x={250}
        text={`Figures: ${figures} | Lines: ${lines} | Rows: ${rows} | Cols: ${cols}`}
        style={
          new TextStyle({
            fontSize: 16,
            fontWeight: 400,
            fill: '#FBA41C'
          })
        }
      />
    </Container>

  )
};
