import React, { useRef, useState, useMemo, useEffect, useLayoutEffect } from 'react';
import { Container } from 'react-pixi-fiber';
import useTimeout from '@rooks/use-timeout';
import { animated, useSpring } from 'react-spring';
import * as PIXI from 'pixi.js';

import { Figure } from './Figure';
import { InfinityRect } from '../utils/figure';

const clickTime = 200; // time in ms

const AnimatedFigure = animated(Figure);

export const SlotsWithFigures = ({
  x: cntX, y: cntY,
  figures,
  height,
  width,
  blockWidth,
  blockHeight,
  onDragEnd,
  onClick,
  view = 'vertical',
  jumpValue = -100,
  animateOnPress=false,
  blockMargin = 1,
  slotCount = 3,
}) => {
  const cntRef = useRef();


  const [figureCoords, setFigureCoords] = useState({});
  const [startPos, setStartPos] = useState();
  const [{ diffX, diffY }, setDiffXY] = useState({ diffX: 0, diffY: 0 });
  const [draggedFigureSlot, setDraggedFigureSlot] = useState(null);

  const [canBeClick, setCanBeClick] = useState(false);
  const { start, stop } = useTimeout(() => {
    setCanBeClick(false)
  }, clickTime);

  const slotHeight = Math.floor(height / slotCount) - 10;
  const slotWidth  = Math.floor(width / slotCount) - 10;

  const getFigurePos = (slot) => {
    if (view === 'horizont') {

    }

    const defaultPos = view === 'horizont' ? {
      x: slotWidth * slot,
      y: 0
    } :
    {
      x: 10,
      y: slotHeight * slot,
    };
    return figureCoords[slot] || defaultPos;
  };

  const getFigureProps = (figure) => {
    return ({
      blockWidth,
      blockHeight,
      blockMargin,

      figure: figure.figure,
      ...getFigurePos(figure.slot)
    });
  }

  function handlePointerDown(e) {
    setCanBeClick(true);
    start();
    const { x, y } = e.data.getLocalPosition(cntRef.current);
    const { x: fx, y: fy } = getFigurePos(this.slot);
    setDiffXY({
      diffX: fx - x,
      diffY: fy - y,
    });

    setDraggedFigureSlot(this.slot);
    setStartPos({x, y});
  }

  const handlePointerMove = (e) => {
    if (draggedFigureSlot !== null) {
      const newPosition = e.data.getLocalPosition(cntRef.current);
      setFigureCoords({
        ...figureCoords,
        [draggedFigureSlot]: {
          x: newPosition.x + diffX,
          y: newPosition.y + diffY,
        },
      });

      if(Math.abs(startPos.x - newPosition.x) > 10 || Math.abs(startPos.y - newPosition.y) > 10) {
        setCanBeClick(false);
      }
    }
  }

  const handlePointerUp = (e) => {
    if (draggedFigureSlot === null) return;
    const figure = figures.find(({ slot }) => slot === draggedFigureSlot);

    setFigureCoords({});
    setDraggedFigureSlot(null);

    stop();
    if (canBeClick) {
      onClick(figure);
      setCanBeClick(false);
    } else {
      onDragEnd({
        figure,
        getLocalPosition: (pixiEl) => {
          const pos = e.data.getLocalPosition(pixiEl);
          return {
            x: pos.x + diffX,
            y: pos.y + diffY + (animateOnPress ? jumpValue : 0),
          };
        },
      });
    }
  }


  return (
    <Container
      ref={cntRef}
      x={cntX} y={cntY}
      hitArea={InfinityRect}
      interactive={true}
      pointerup={handlePointerUp}
      pointermove={handlePointerMove}>
      {figures.map((figure) => {
        return (
          <JumpedFigure
            key={figure.slot}
            {...getFigureProps(figure)}
            interactive={true}
            pointerdown={handlePointerDown.bind(figure)}
            jumpValue={jumpValue}
            scale={animateOnPress ? 0.5 : 1}
            startJumpAnimation={animateOnPress && figure.slot === draggedFigureSlot && !canBeClick}
          />
        );
      })}
    </Container>
  );
}


function JumpedFigure(props) {
  const {
    blockWidth,
    blockHeight,
    blockMargin,
    figure,
    jumpValue,
    scale,
    startJumpAnimation,
    ...anotherProps
  } = props;

  const [animatedProps, animatedApi] = useSpring(() => ({
    from: { scale, y: 0 },
  }));

  const hitArea = useMemo(() => new PIXI.Rectangle(
    -10, 
    -10, 
    figure[0].length * blockWidth + 20, 
    figure.length * blockHeight + 20,
  ), [figure]);

  useLayoutEffect(() => {
    if (startJumpAnimation) {
      animatedApi.start({ y: jumpValue, scale: 1, config: { duration: 100 }})
    } else {
      animatedApi.set({ scale, y: 0 });
    }
  }, [startJumpAnimation]);

  return (
    <Container hitArea={hitArea} {...anotherProps}>
      <AnimatedFigure 
        {
          ...{
            blockWidth,
            blockHeight,
            blockMargin,
            figure,
            scale,
          }
        }
        x={0}
        y={0}
        {...(startJumpAnimation && animatedProps || {})}
      />
    </Container>
  );
}