import React, { forwardRef, useMemo } from 'react';
import { Container } from 'react-pixi-fiber';
import { Block } from './Block';
import * as PIXI from 'pixi.js';

export const Figure = forwardRef(({
  figure,
  blockWidth,
  blockHeight,
  blockMargin=0,
  ...props
}, ref) => {

  const blocksList = [];

  const hitArea = useMemo(() => new PIXI.Rectangle(
    -10, 
    -10, 
    figure[0].length * blockWidth + 20, 
    figure.length * blockHeight + 20,
  ), [figure]);

  for(let y = 0; y < figure.length; y++) {
    const row = figure[y];
    for(let x = 0; x < row.length; x++) {
      if (row[x]) {
        blocksList.push(
          <Block
            key={`${x}:${y}`}
            width={blockWidth - blockMargin * 2}
            height={blockHeight - blockMargin * 2}
            x={x * blockWidth}
            y={y * blockHeight}
          />);
      }
    }
  }

  return (
    <Container 
      ref={ref} 
      hitArea={hitArea}
      interactiveChildren={false}
      { ...props }>
      {blocksList}
    </Container>
    
  );
});
