import React, { useReducer, useRef, forwardRef, useState, useLayoutEffect, useCallback } from 'react';
import { Sprite, Graphics, Stage, usePixiTicker, withApp } from "react-pixi-fiber";
import * as PIXI from "pixi.js";

import blockImg from '../block.png';


export const BlockTexture = PIXI.Texture.from(blockImg);

export const Block = forwardRef((props, ref) => {
  return <Sprite ref={ref} texture={BlockTexture} {...props} />;
});
