import * as PIXI from 'pixi.js';

export const InfinityRect = new PIXI.Rectangle(
  Number.MIN_SAFE_INTEGER, 
  Number.MIN_SAFE_INTEGER, 
  Number.POSITIVE_INFINITY, 
  Number.POSITIVE_INFINITY);

export function rotateFigureLeft(figure) {
  const newFigure = [];
  for(let y = 0; y < figure.length; y ++) {
    const row = figure[y];
    
    for(let x = 0; x < row.length; x++) {
      newFigure[x] = newFigure[x] || [];
      newFigure[x][y]=row[x];
    }
  }
  return newFigure;
}

export function reverseFigure(figure) {
  return figure.reverse();
}

export function rotateFigureRight(figure) {
  const newFigure = [];
  for(let y = 0; y < figure.length; y ++) {
    const row = figure[y];
    
    for(let x = 0; x < row.length; x++) {
      newFigure[x] = newFigure[x] || [];
      newFigure[x][figure.length - 1 - y]=row[x];
    }
  }
  return newFigure;
}